.doneListContainer{
    border: 3px solid #f2f2f2;
    height: 500px;
    width: 500px;
    margin: auto; 
    border-radius: 12px;
    background-color: white;
    margin-top: 20px;
    margin-bottom: 40px;
}

.doneUlContainer{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 0;
    list-style: none;
}

.doneUlContainer > li {
    text-decoration: line-through;
}